.App {
  background: linear-gradient(139deg, rgb(21 20 137) 0%, rgb(9 8 24) 100%);
  text-align: center;
  /* width: 100vw; */
  /* height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Source Sans Pro,Hiragino Sans GB,Arial,sans-serif;
  font-size: 14px;
  color: #fff;
  overflow: auto;
}

.widget {
  background: rgb(255 255 255 / 18%);
  text-align: left;
}
.widget > .textContainer{
  margin: 5%;
  margin-bottom: 2%!important;
}
.widget-number{
  font-size: calc(10px + 2vmin);
  font-weight: 600;
}
.widget-info{
  font-size: calc(9px + 1.5vmin);
  font-weight: 400;
  font-variant: small-caps;
}
*, ::after, ::before {
  box-sizing: unset!important;
}
.graphContainer{
  margin-bottom: 3%;
}

@media (max-width: 576px) {
  /* .container {
    max-width: ;
  } */

  .ant-col {
    display: block!important;
    flex: 0 0 98%!important;
    max-width: 98%!important;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 520px;
  }

  .ant-col {
    display: block!important;
    flex: 0 0 98%!important;
    max-width: 98%!important;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }

  .ant-col {
    display: block!important;
    flex: 0 0 47%!important;
    max-width: 47%!important;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }

  .ant-col {
    display: block!important;
    flex: 0 0 48%!important;
    max-width: 48%!important;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1200px;
  }

  .ant-col {
    display: block!important;
    flex: 0 0 45%!important;
    max-width: 45%!important;
  }
}

.ant-row{
  justify-content: space-between;
  margin-top: 1rem;
  margin-bottom: 1rem
}

.recharts-cartesian-grid-horizontal line {
  stroke-dasharray: 2, 2;
  stroke: #D0D0DA;
}

.recharts-cartesian-axis-tick-value tspan {
  fill: #A1A1B5;
  letter-spacing: 0.03rem;
  font-size: 14px;
}

.recharts-cartesian-axis.recharts-yAxis .recharts-cartesian-axis-tick {
}

.recharts-tooltip-cursor {
fill: rgba(204, 204, 204, 0.2);
}

.recharts-surface {
  overflow: visible!important;
}

.recharts-default-tooltip {
   background-color: rgb(0 0 0 / 90%) !important;
   border-radius: 10px;
   border-width: 0px!important;
 }